/* Style général du conteneur de la page */
.projects-page {
  position: relative;
  padding-top: 70px;
  padding-bottom: 70px;
  min-height: 100vh;
  background: none;
  background-color: #fff;
  z-index: 0;
}

/* Fond pour chaque catégorie */
.projects-page.software {
  background: linear-gradient(to top, #00a6f9, #ffffff);
}
.projects-page.hardware {
  background: linear-gradient(to top, #4e4e4e, #ffffff);
}
.projects-page.apprentissage {
  background: linear-gradient(to top, #ffb300, #ffffff);
}

/* Titre centré */
.projects-title {
  text-align: center;
  margin-bottom: 10px;
}

/* Conteneur principal centré en Flexbox */
.projects-content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px; /* Espace entre le menu et la vidéo */
  width: 100%;
  min-height: 500px;
}

/* Menu onglet */
.tabs-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

/* Vidéo et légende */
.video-column {
  width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.video-wrapper {
  position: relative;
  width: 100%;
  height: 450px;
  background-color: #ddd;
  border-radius: 8px;
  overflow: hidden;
  opacity: 0;
  animation: fadeIn 2s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.video-caption {
  margin-top: 10px;
  font-size: 1.2rem;
  color: #333;
  text-align: left;
  opacity: 0;
  transform: translateY(20px);
  animation: slideFadeIn 1s forwards;
  animation-delay: 0.3s;
}

@keyframes slideFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Sous-titre au-dessus de la vidéo */
.video-subtitle {
  margin-bottom: 10px;
  font-size: 1.3rem;
  color: #333;
  text-align: center;
  opacity: 0;
  transform: translateY(10px);
  animation: slideFadeIn 0.8s forwards;
}

/* Style des onglets */
.tab {
  background: #eee;
  border: none;
  padding: 15px 20px;
  cursor: pointer;
  border-radius: 8px;
  font-weight: bold;
  font-size: 1.1rem;
  transition: background 0.3s ease, transform 0.3s ease, color 0.3s ease;
  width: 180px;
  text-align: center;
  color: #333;
}

.tab:hover,
.tab.active {
  transform: scale(1.05);
}

.tab.software:hover,
.tab.software.active {
  background: #00a6f9;
  color: #fff;
}

.tab.hardware:hover,
.tab.hardware.active {
  background: #00b700;
  color: #fff;
}

.tab.apprentissage:hover,
.tab.apprentissage.active {
  background: #ffb300;
  color: #fff;
}

/* Responsive */
@media (max-width: 768px) {
  .projects-page {
    padding-top: 50px;
  }
  
  .projects-content {
    flex-direction: column;
    align-items: center;
    margin: 0 10px;
  }
  
  .tabs-column,
  .video-column {
    width: 100%;
    display: flex;
    align-items: center;
  }
  
  .video-wrapper {
    width: 100%;
    height: auto;
    max-width: 100%;
    aspect-ratio: 16/9;
  }
  
  .video-caption {
    text-align: center;
  }
}
